.input-anime {
  border: none;
  position: relative;
  overflow: hidden;
}

.input-anime span:nth-child(1), .input-anime span:nth-child(2), .input-anime span:nth-child(3), .input-anime span:nth-child(4) {
  position: absolute;
}

.input-anime span:nth-child(3) {
  width: 150%;
  height: 1px;
  background: linear-gradient(90deg, #000, #2fe8ff, #000);
  animation: animateBottom 1s linear infinite paused;
  bottom: 0;
  left: 0;
}

@keyframes animateBottom {
  0% {
    transform: translateX(-100%);
  }

  100% {
    transform: translateX(100%);
  }
}

.input-anime:hover span, .input-anime:focus span {
  animation-play-state: running;
}

.blick {
  text-decoration: none;
  display: inline-block;
  position: relative;
  overflow: hidden;
}

.blick:before {
  content: "";
  width: 1.5em;
  height: 100%;
  background-color: rgba(255, 255, 255, .6);
  animation: move-light 2s infinite;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-10em)skewX(-45deg);
}

@keyframes move-light {
  from {
    transform: translateX(-10em)skewX(-45deg);
  }

  to {
    transform: translateX(15em)skewX(-45deg);
  }
}

